
@import "../component.scss";

.list{
	position: relative;
	ul{
		display: table;
		width: 100%;
		li{
			display: table-row;
			@include clear;
			& > *{
				display: table-cell;
				padding: 25px 0;
				border-bottom: 1px solid #ccc;
				vertical-align: middle;
			}
			.date,
			.etc{
				width: 15%;
				text-align: center;
			}
		}
	}
	.btn-more{
		position: absolute;
		top: -40px;
		right: 20px;
	}
}

.multi-list{
	li{
		margin-top: 10px;
		&:first-child{
			margin-top: 0;
		}
		.btn ~ .selectbox {
			margin-left: 26px;
		}
	}
}

.table {
	&-vertical {
		> ul {
			display: table;
			width: 100%;
			> li {
				display: table-row;
				> * {
					vertical-align: middle;
					display: table-cell;
					padding: {
						top: 25px;
						bottom: 25px;
						left: 20px;
					};
					color: #888;
					border-bottom: 1px solid #ccc;
				}

			}
			.cell-time{
				width: 10%;
			}
			.cell-date,
			.cell-etc{
				width: 13%;
			}
		}
	}
	&-thead {
		> ul {
			padding-top: 44px;
			> li {
				&:first-child {
					> * {
						position: relative;
						&:before {
							content: attr(data-title);
							font-weight: 700;
							text-align: center;
							letter-spacing: -0.5px;
							line-height: 44px;
							position: absolute;
							top: -45px;
							left: 0;
							width: 100%;
							height: 44px;
							border-bottom: 1px solid #ccc;
						}
					}
				}
			}
		}
	}
	&-horizontal {
		.bg-info{
			background-color: #f9f9f9;
		}
		.th,
		.td {
			box-sizing: border-box;
			display: table-cell;
		}
		.th {
			font-weight: 700;
			letter-spacing: -0.5px;
			width: 43%;
			padding: {
				top: 26px;
				bottom: 26px;
				left: 20px;
			}
		}
		.td {
			vertical-align: middle;
			font-size: 14px;
			color: #888;
			font-weight: 700;
			padding: {
				top: 18px;
				bottom: 17px;
				left: 20px;
			}
			strong{
				color: #000;
			}
		}
		.full {
			width: 100%;
			.th {
				width: 17.6%;
			}
		}
		> ul {
			@include clear;
			> li {
				display: table;
				min-height: 75px;
				border-bottom: 1px solid #ccc;

				&.odd {
					clear: left;
					float: left;
					width: 46%;
					padding-right: 4%;
					.th {
						width: 38.5%;
					}
				}
				&.even {
					float: right;
					width: 50%;
				}
				.help {
					color: #666;
					font-size: 14px;
					text-align: right;
					display: block;
					margin-top: 10px;
				}
				.form {
					padding-left: 0;
					.input-inline {
						& ~ .help {
							text-align: left;
							display: inline-block;
							margin: {
								top: auto;
								left: 16px;
							}
						}
					}
				}
			}
		}
	}
	&-no-heading {
		> ul {
			> li {
				&:first-child {
					> * {
						&:before {
							color: #000;
							border-bottom-color: #000;
						}
					}
				}
			}
		}
	}
	&-border{
		border-top: 1px solid #000;
	}
	.text-center{
		padding-left: 0;
		text-align: center;
	}
}

/* pagination */
.pagination {
	&-wrap {
		margin-bottom: 100px;
	}
	&-list {
		@include clear;
		li {
			float: left;
			width: 31px;
			height: 31px;
			line-height: 31px;
			border: 1px solid #ccc;
			text-align: center;
			font-size: 12px;

			& + li {
				border-left: 0;
			}
			a {
				display: block;
				width: 100%;
				height: 100%;
				position: relative;
				color: #0061a0;
				&.active {
					background-color: #0061a0;
					color: #fff;
				}
				span {
					display: block;
					width: 5px;
					height: 5px;
					border: 0px solid #ccc;
					border-width: 1px 1px 0 0;
					position: absolute;
					top: 50%;
					&.page-before {
						transform: translate(-50%, -50%) rotate(-135deg);
						left: 50%;
						&:last-child {
							left: 60%;
						}
					}
					&.page-after {
						transform: translate(45%, -50%) rotate(45deg);
						right: 45%;
						&:last-child {
							right: 55%;
						}
					}
				}
			}
		}
	}
}